<script>
import $ from "jquery";
import {FormWizard, TabContent ,ValidationHelper} from 'vue-step-wizard'
import { required } from "vuelidate/lib/validators";


export default {
  name: "WomanStory",
  components: {
    FormWizard,
    TabContent
  },
  mixins: [ValidationHelper],
  data() {
    return {
      active: "Item1",
      formData: {
        Q1:'',
        Q2:'',
        Q3:'',
        Q4:'',
        Q5:'',
        Q6:'',
        Q7:'',
        Q8:'',
        Q9:'',
        Q10:'',
        Q11:'',
        Q12:''
      },
      validationRules: [
        {Q1:{ required }},
        {Q2:{required}},
        {Q3:{ required }},
        {Q4:{required}},
        {Q5:{ required }},
        {Q6:{required}},
        {Q7:{ required }},
        {Q8:{required}},
        {Q9:{ required }},
        {Q10:{required}},
        {Q11:{ required }},
        {Q12:{required}}
      ],
      showModal: false,
      modalText: '',
      Total:'',
    };
  },
  mounted: function() {
    var hash = window.location.hash;
    var index = hash.lastIndexOf("#");
    if (index != -1) {
      var id = hash.substring(index + 1, hash.length + 1);
      var div = document.getElementById(id);
      if (div) {
        setTimeout(function() {
          $("html, body").animate(
            { scrollTop: $(div).offset().top - 140 },
            500
          );
        });
      }
    }
  },
  methods: {
    selectItem(item) {
      this.active = item;
    },
    // onComplete() {
    //   alert("Submitting Form ! Rock On");
    // },
    onComplete() {
      let a = this.formData.Q1;
      let b = this.formData.Q2;
      let c = this.formData.Q3;
      let d = this.formData.Q4;
      let e = this.formData.Q5;
      let f = this.formData.Q6;
      let g = this.formData.Q7;
      let h = this.formData.Q8;
      let i = this.formData.Q9;
      let j = this.formData.Q10;
      let k = this.formData.Q11;
      let l = this.formData.Q12;
      let Total = parseInt(a) 
      + parseInt(b)
      + parseInt(c)
      + parseInt(d)
      + parseInt(e)
      + parseInt(f)
      + parseInt(g)
      + parseInt(h)
      + parseInt(i)
      + parseInt(j)
      + parseInt(k)
      + parseInt(l);
      this.Total = Total;
      this.showModal = true
      this.$bvModal.show('my-modal')
      this.modalText = 'Whatever you want to show'
    },
  },
};
</script>

