import $ from "jquery";

export default {
  name: "serve3",
  data() {
    return {
      active: "Item1",
    };
  },
  mounted: function() {
    var hash = window.location.hash;
    var index = hash.lastIndexOf("#");
    if (index != -1) {
      var id = hash.substring(index + 1, hash.length + 1);
      var div = document.getElementById(id);
      if (div) {
        setTimeout(function() {
          $("html, body").animate(
            { scrollTop: $(div).offset().top - 140 },
            500
          );
        });
      }
    }
  },
  methods: {
    selectItem(item) {
      this.active = item;
    },
  },
};
